/* Base colors */
body {
  font-family: "Montserrat", sans-serif; }

h1, h2, h3, h4, h5, h6 {
  font-family: "Pacifico", cursive;
  margin-top: 0; }

@font-face {
  font-family: 'fontastic';
  src: url("../assets/fonts/fontastic/fontastic.eot");
  src: url("../assets/fonts/fontastic/fontastic.eot?#iefix") format("embedded-opentype"), url("../assets/fonts/fontastic/fontastic.woff") format("woff"), url("../assets/fonts/fontastic/fontastic.ttf") format("truetype"), url("../assets/fonts/fontastic/fontastic.svg#fontastic") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block; }

[data-icon]:before {
  font-family: "fontastic" !important;
  content: attr(data-icon);
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

[class^="icon-"]:before,
[class*=" icon-"]:before {
  font-family: "fontastic" !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  speak: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.icon-facebook:before {
  content: "\61"; }

.icon-twitter:before {
  content: "\62"; }

.icon-youtube:before {
  content: "\63"; }

.icon-mail:before {
  content: "\64"; }

.icon-location:before {
  content: "\65"; }

.icon-phone:before {
  content: "\66"; }

.icon-menu:before {
  content: "\67"; }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }
  *::before, *::after {
    box-sizing: border-box; }

html,
body {
  height: 100%;
  width: 100%;
  margin: 0; }

body {
  background-color: #FFFFFF;
  font-size: 16px;
  line-height: 1.4; }

a {
  text-decoration: none;
  color: #FFFFFF;
  transition: color .3s linear; }

img {
  max-width: 100%;
  height: auto;
  display: block; }

ul, nav {
  padding-left: 0;
  list-style: none; }

.page {
  width: 100%;
  height: 100%;
  position: relative; }

.container {
  margin: 0 auto;
  padding: 0 20px;
  position: relative;
  width: 100%; }
  @media screen and (min-width: 768px) {
    .container {
      max-width: 708px; } }
  @media screen and (min-width: 1084px) {
    .container {
      max-width: 1024px; } }
  @media screen and (min-width: 1920px) {
    .container {
      max-width: 1860px; } }

button, .btn {
  border: none;
  background-color: #FF0000;
  color: #000000;
  padding: 10px 14px;
  cursor: pointer;
  font-size: 13px;
  transition: background-color 0.2s linear; }
  button:hover, .btn:hover {
    background-color: #BC3A80; }

.btn-prev, .bth-next {
  position: relative;
  margin-right: 6px;
  margin-top: -8px;
  z-index: 8; }

.main-header {
  background-image: url(../assets//images/banner.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.3); }

.hero {
  display: flex;
  flex-direction: column; }

.is-full-height {
  min-height: 50vh; }
  @media screen and (min-width: 768px) {
    .is-full-height {
      min-height: 75vh; } }

.main-header-head {
  background-color: transparent;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 1; }

.main-header-body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-grow: 1;
  padding: 40px 20px; }

.header-text {
  color: #FFFFFF;
  font-size: 32px;
  line-height: 1.4;
  font-weight: 500;
  letter-spacing: 0.1em; }

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 90px; }
  .nav .contact-info {
    color: #FFFFFF;
    display: none; }
  @media screen and (min-width: 768px) {
    .nav {
      flex-direction: row; }
      .nav .contact-info {
        display: block; } }

.nav-bl-contact-info {
  display: flex;
  flex-direction: column; }
  @media screen and (min-width: 768px) {
    .nav-bl-contact-info {
      flex-direction: row;
      justify-content: space-around; } }

#nav-toggle a:hover,
#nav-menu a:hover {
  padding: 0;
  border-bottom: 2px solid red; }

#nav-toggle {
  display: block;
  top: 50px;
  left: 50px;
  z-index: 1;
  -webkit-user-select: none;
  user-select: none;
  /*
   * Just a quick hamburger
   */
  /* 
    * Transform all the slices of hamburger
    * into a crossmark.
    */ }
  #nav-toggle input {
    display: block;
    width: 40px;
    height: 32px;
    position: absolute;
    top: 20px;
    right: 16px;
    cursor: pointer;
    opacity: 0;
    z-index: 2;
    -webkit-touch-callout: none; }
  #nav-toggle span {
    display: block;
    width: 33px;
    height: 4px;
    margin-bottom: 5px;
    position: relative;
    background-color: #FFFFFF;
    border-radius: 3px;
    z-index: 1;
    transform-origin: 4px 0px;
    transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease; }
    #nav-toggle span:first-child {
      transform-origin: 0% 0%; }
    #nav-toggle span:nth-last-child(2) {
      transform-origin: 0% 100%; }
  #nav-toggle input:checked ~ span {
    opacity: 1;
    transform: rotate(45deg) translate(-2px, -1px);
    background: #cdcdcd;
    /*
    * But let's hide the middle one.
    */
    /*
    * Ohyeah and the last one should go the other direction
    */ }
    #nav-toggle input:checked ~ span:nth-last-child(3) {
      opacity: 0;
      transform: rotate(0deg) scale(0.2, 0.2); }
    #nav-toggle input:checked ~ span:nth-last-child(2) {
      transform: rotate(-45deg) translate(0, -1px); }
  #nav-toggle input:checked ~ ul {
    transform: none; }
  @media screen and (min-width: 768px) {
    #nav-toggle {
      display: none; } }

#menu {
  position: absolute;
  width: 300px;
  margin: -100px 0 0 -190px;
  padding: 30px;
  padding-top: 125px;
  background-color: #000000;
  list-style: none;
  -webkit-font-smoothing: antialiased;
  /* to stop flickering of text in safari */
  transform-origin: 0% 0%;
  transform: translate(100%, 0);
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1); }
  #menu li {
    padding: 10px 0;
    font-size: 22px; }

#nav-menu {
  display: none; }
  #nav-menu ul {
    display: flex; }
    #nav-menu ul > li {
      padding: 10px; }
  @media screen and (min-width: 768px) {
    #nav-menu {
      display: block; } }
